.comment {
    box-sizing: border-box;
    width: 100%;

    position: relative;

    line-height: 20px;
}

.textarea {
    box-sizing: border-box;
    width: 100%;

    padding: 12px 70px 12px 16px;

    min-height: 84px;

    color: #96A1A7;

    resize: none !important;
    overflow: hidden;
}

.right-text {
    position: absolute;
    top: 22px;
    right: 16px;

    transform: translateY(-50%);

    color: #3C6CCE;
}